	* {
		box-sizing: border-box;
		font-family: 'Roboto', sans-serif;
	}

	.container {
		width: auto;
		margin: 20px;
		color: black;
		width: 450px;
		overflow: hidden;
		user-select: none;
	}

	.calc-body {
		margin: auto;
		border: solid 1px #3A4655;
		box-shadow: 0 8px 50px -7px black;
		user-select: none;
	}

	.calc-screen {
		background: #3A4655;
		width: 100%;
		min-height: 150px;
		padding: 20px;
	}

	.calc-operation {
		text-align: right;
		color: #727B86;
		font-size: 21px;
		padding-bottom: 10px;
		border-bottom: dotted 1px;
	}

	.calc-typed {
		margin-top: 20px;
		font-size: 45px;
		text-align: right;
		color: #fff;
		word-break: break-all;
	}

	.calc-button-row {
		width: 100%;
		background: #3C4857;
	}

	.button {
		width: 25%;
		background: #425062;
		color: #fff;
		padding: 20px;
		display: inline-block;
		font-size: 25px;
		text-align: center;
		vertical-align: middle;
		margin-right: -4px;
		border-right: solid 2px #3C4857;
		border-bottom: solid 2px #3C4857;
		user-select: none;
	}

	.button:hover {
		background-color: #243956;
	}

	html {
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: #2d3436;
		background-image: linear-gradient(315deg, #2d3436 0%, #000000 74%);
		font-family: 'Cookie', cursive;
	}

	.title:hover {
		background: #E0B612;
		color: #fff;
	}

	.title {
		margin-bottom: 10px;
		padding: 5px 0;
		font-size: 40px;
		font-weight: bold;
		text-align: center;
		color: #AEB3BA;
		font-family: 'Cookie', cursive;
	}

	input[type=button] {
		width: 60px;
		height: 60px;
		float: left;
		padding: 0;
		margin: 5px;
		box-sizing: border-box;
		background: #ecedef;
		border: none;
		font-size: 30px;
		line-height: 30px;
		border-radius: 50%;
		font-weight: 700;
		color: #5E5858;
		cursor: pointer;
	}

	input[type=text] {
		width: 270px;
		height: 60px;
		float: left;
		padding: 0;
		box-sizing: border-box;
		border: none;
		background: none;
		color: red;
		text-align: right;
		font-weight: 700;
		font-size: 60px;
		line-height: 60px;
		margin: 0 25px;
	}

	.calculator {
		background-color: #c0c0c0;
		box-shadow: 0px 0px 0px 10px #666;
		border: 5px solid black;
		border-radius: 10px;
	}

	.calculate {
		background-color: #cb4f30;
		border-radius: 5px;
	}

	.calculate:hover {
		background-color: #DD765D;
	}